.wrapper {
    display: flex;
    min-width: 1200px;
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    flex-direction: column;
}

.footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    padding: 20px;
    background-color: #2039a3;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin-bottom: 20px;
    flex-direction: column;

    img {
        cursor: pointer;
    }
}

.df {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.copy-right {
    background-color: #131329;
    color: var(--white);
    display: flex;
    justify-content: center;
    padding: 20px;
}

.pay-transport {
    display: flex;
    justify-content: space-around;
    margin: 30px 0;
}

.pay {
    flex: 1;
    display: flex;
    flex-direction: column;
    span {
        font-weight: bold;
        margin: 20px 0 20px 0;
    }
}

.box-img {
    display: flex;
    justify-content: space-around;
}

.transport {
    flex: 1;
    display: flex;
    flex-direction: column;
    span {
        font-weight: bold;
        margin: 20px 0 20px 0;
    }
}

.img {
    margin-right: 30px;
    cursor: pointer;
}

.main {
    display: flex;
}

.col {
    display: flex;
    flex: 1;
    flex-direction: column;

    ul li {
        margin: 10px;
    }

    ul li Link:hover {
        color: var(--notic);
    }
}

.logo {
    width: 200px;
}

.line {
    margin: 10px 0px;
}

.label {
    font-weight: bold;
    font-size: 2rem;
    margin-bottom: 20px;
}

.input {
    display: flex;
    margin: 10px 0;
    width: 100%;
    cursor: pointer;

    input {
        border: none;
        outline: none;
        width: 100%;
    }
}
