.wrapper {
    display: flex;
    min-width: 1200px;
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    flex-direction: column;
}

// Slide

.slide-content {
    width: 800px;
}

.slider {
    width: 100%;
    margin-top: 20px;
    display: flex;
}
.img-slide {
    width: 800px;
    min-height: 250px;
    max-height: 250px;
    border-radius: 8px;
}

.img-ads-slider {
    width: 378px;
    height: 250px;
    border-radius: 8px;
}

// Endow

.menu-endow {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 8px;
}

.menu-endow__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &:hover {
        color: var(--notic);
    }
}

.img-endow {
    width: 48px;
    height: 48px;
}

.img-vs {
    width: 120px;
    height: 88px;
}

.span-endow {
    margin-top: 10px;
    font-weight: bold;
    color: black;
}

// Flash sale

.flash-sale {
    width: 100%;
    height: 414px;
    background-color: var(--red);
    margin-top: 20px;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 8px;
}

.fs-header {
    width: 100%;
    height: 79px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.img-fs {
    width: 200px;
    height: 50px;
}

.time-cd {
    width: 576px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    span {
        font-size: 1.5rem;
    }
}

.time {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    background-color: var(--primary);
    padding: 10px;
    width: 40px;
    height: 40px;
    border-radius: 8px;
    color: var(--white);
    user-select: none;
}

.fs-slider {
    padding: 10px;
    width: 100%;
    margin-left: 3px;
}

// Featured list
.featured-list {
    width: 100%;
    margin-top: 20px;
    border: 1px solid transparent;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 8px;
}

.featured-list__header {
    width: 100%;
    color: var(--primary);
    border-bottom: 5px solid var(--primary);
    padding: 20px;
}

.featured-list__body {
    width: 100%;
    display: flex;
    padding: 20px;
    justify-content: space-between;
}

.featured-list__item {
    width: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold;
    color: black;

    &:hover {
        color: var(--notic);
    }
}

.img-featured-list {
    width: 87px;
    height: 87px;
}

// ADS

.ads {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;

    a:nth-child(2) {
        margin: 0 20px;
    }
}

.img-ads {
    width: calc(100% / 3);
    height: 148px;
    position: relative;

    img {
        width: 100%;
    }
}

.news {
    width: 100%;
    margin-top: 20px;
    border: 1px solid transparent;
    box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-radius: 8px;
}

.news-header {
    padding: 20px;
    margin-bottom: 20px;
    border-bottom: 5px solid var(--primary);
}
